  border: none;
}

.navigationbar{
    height:100%;
    width:15vw;
    position: fixed;
    top:0;
    left:0;
    color:#001a33;
    background-color:#DFF1FF;
    padding-top: 50px;
}

.navigationbar a{
  text-decoration-line: none;
  color:#001a33;
}

.navigationbar a:hover{
  color:#83D479;
}

